import '../css/Home.css'
import DownloadIcon from '@mui/icons-material/Download';
import { useEffect, useState } from 'react';
function Home() {
    const [pic, setPic] = useState('')
    useEffect(() => {
        fetch('https://portfolio.sagargujarathi.me/profile')
            .then(data => data.json())
            .then(data => setPic(data))
    }, [])
    return (
        <div className="home">
            <div className="home-left-container">
                <div className="intro-text">
                    <div className="intro-greet">
                        Hey There!
                        <span className='fire'>I'm Sagar Gujarathi</span>
                    </div>
                    <span className='intro-quote'>A passionate developer who turns what may seem impossible into a reality.</span>
                    <a href="https://github.com/SagarGujarathi/Resume/blob/main/Resume.pdf" style={{ color: 'inherit', fontFamily: 'inherit' }} target='_blank'>
                        <div className='download-cv-button'>Download CV <DownloadIcon /></div>
                    </a>
                </div>
            </div>
            <div className="home-right-container">
                {
                    <img src={pic} className='my-pic' />
                }
            </div>
        </div >
    )
}

export default Home